body{
  background-color: #9266ff;

}
.App{
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}
#ToDo_List input{
  background-color: rgb(27, 112, 137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;
}
#ToDo_List input::placeholder{
  color: rgba(255, 255, 255, 0.5);
}
#ToDo_List input:focus, #ToDo_List button:focus{
  outline: none;
}
#ToDo_List button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(202,156,24);
  cursor: pointer;
  font-weight: bolder;
  color: #fff
}
